.slider-bg {
    background-color: #E5E5E5;
    
}

.slider-title {
    padding-top: 20px;
    text-align: center;
}

.slider-list {
    height: 220px;
    margin: auto;
    position: relative;
    width: 90%;
    display: grid;
    place-items: center;
    overflow: hidden;

}

.slider-list::before,
.slider-list::after {
    background: linear-gradient(to right, rgba(255, 255, 255, 1) 0%,
    rgba(255, 255 ,255, 0) 90%);
    content: '';
    height: 100%;
    position: absolute;
    width: 15%;
    z-index: 2;
}
.slider-list::before {
    left: 0;
    top: 0;
}
.slider-list::after {
    right: 0;
    top: 0;
    transform: rotateZ(180deg);
}
.slide-track {
    display: flex;
    width: calc(250px * 20);
    animation: scroll 30s linear infinite;
}

@keyframes scroll {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(calc(-250px * 2));
    }
}

.slider-element {
    height: 150px;
    width: 100px;
    display: flex;
    align-items: center;
    padding: 8px;
    perspective: 100px;
    border-radius: 5px;
}

img {
    transform: scaleX(1);
    transition: transform 250ms linear;
    border-radius: 5px;
    transition: transform 1s;
}

img:hover {
    cursor: pointer;
    transform: scale(1.10);

}